<script>

export default {
    name: 'forms',
    components:{
        
    },
    data (){
    return{
      API_URL: "api/",
      results: [],
      examno: '',
      submitted: true,
      count: '',
      color: '',
      branch:'',
      loading: false
    }
  },  
  methods: {
    goBack(){
    this.submitted = true;
  },
    getResults(){
       this.loading=true;
       var self = this;
       setTimeout(function () {
                self.loading=false;
            }, 4000);
        
      if(this.examno >=  20190001 && this.examno <= 20190313){
        fetch(this.API_URL + "result/science/"+this.examno)
        .then(res => res.json())
      .then(json => {
        this.results = json;
        this.submitted = false;
        this.count = Object.keys(this.results.result[0]).length;
        this.branch = 'science';
        if((this.results.result[0].RESU)=='FAIL' || (this.results.result[0].RESU)=='ABSENT')
        {
          this.color = 'red'
        }
        else  
          this.color = 'green';
      })
      }

      else if(this.examno >=  20190401 && this.examno <= 20190844){
        fetch(this.API_URL + "result/commerce/"+this.examno)
        .then( data => {
        return data.json();
      })
      .then(json => {
        this.results = json;
        this.submitted = false;
        this.branch = 'commerce';
        this.count = Object.keys(this.results.result[0]).length;
        if((this.results.result[0].RESU)=='FAIL' || (this.results.result[0].RESU)=='ABSENT')
        {
          this.color = 'red'
        }
        else  
          this.color = 'green';
      });
      }
       else if(this.examno >=  20190901 && this.examno <= 20191221){
        fetch(this.API_URL + "result/education/"+this.examno)
        .then( data => {
        return data.json();
      })
      .then(json => {
        this.results = json;
        this.submitted = false;
        this.branch = 'education';
        this.count = Object.keys(this.results.result[0]).length;
        if((this.results.result[0].RESU)=='FAIL' || (this.results.result[0].RESU)=='ABSENT')
        {
          this.color = 'red'
        }
        else  
          this.color = 'green';
      });
      }
      else if(this.examno >=  20191301 && this.examno <= 20191653){
        fetch(this.API_URL + "result/political/"+this.examno)
        .then( data => {
        return data.json();
      })
      .then(json => {
        this.results = json;
        this.submitted = false;
        this.branch = 'political';
        this.count = Object.keys(this.results.result[0]).length;
        if((this.results.result[0].RESU)=='FAIL' || (this.results.result[0].RESU)=='ABSENT')
        {
          this.color = 'red'
        }
        else  
          this.color = 'green';
      });
      }
      else{
        alert("Inavalid Exam No");
      }
      
    },
    beforeMount(){
        this.getResults;
    }
  }
  
}
</script>

